import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { cleanCart } from '../store/cartSlice'
import { replaceTemplate } from '../utils';
import { Helmet } from 'react-helmet-async';

export default function Order() {
	
	//    
	const dispatch = useDispatch();

	//   
	const { cart } = useSelector(state => state.cart);

    //     
    const cartCount = cart.length;

	//     
	const [formVisible, setFormVisible] = useState(true);
	const [successVisible, setSuccessVisible] = useState(false);
	const [emptyCartVisible, setEmptyCartVisible] = useState(false);
	
	//    
	const [formData, setFormData] = useState({
        name: " ",
        phone: "+38 (093) 01 - 02 - 003",
        address: ", . ",
    });
	
	//       useEffect
	useEffect(() => {
		if (cartCount === 0) {
		  setFormVisible(false);
		  setSuccessVisible(false);
		  setEmptyCartVisible(true);
		}
	}, [cartCount]); //   cartCount

	//     
	const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

	//    
    const handleSubmit = (e) => {
        e.preventDefault();

		//  
		const orderProducts = cart.map(item => `${item.title} (${item.count})`);

		//     
		const sendData = {
			...formData,
			to: 'hukvadim@gmail.com',
			subject: ' ',
			message: '.    email  222.',
			orderProducts: orderProducts.join('<br>')
		};
		
		
		//  emailSendOrder.html    
		fetch(`https://hukvadim.github.io/react-shop/emailSendOrder.html`)
			.then((response) => response.text())
			.then((template) => {

				//  email 
				sendData['message'] = replaceTemplate(template, sendData);

				//       
				fetch('https://api.inderio.com/send-email', {
					method: 'POST',
					headers: { 'Content-Type': 'application/json' },
					body: JSON.stringify(sendData),
				})
					.then((response) => response.json())
					.then((data) => {
						setFormVisible(false);
						setSuccessVisible(true);
						setEmptyCartVisible(false);

						setTimeout(() => {
							dispatch(cleanCart());
						}, 3000)
					})
					.catch((error) => {
						console.error(':', error);
					});
			});
		
    };

	return (
		<div className="article">
			<Helmet>
                <title> </title>
            </Helmet>
			<div className="container">
				<h2 className="article__title"> </h2>

				<div className="page-content">

					<form id="order-form" onSubmit={handleSubmit} className={!formVisible ? 'hidden' : ''}>
						<p>        ,  , '     :</p>
                        <div className="form-group">
                            <label htmlFor="name">':</label>
                            <input
                                type="text"
                                id="name"
                                name="name"
                                className="form-control"
                                required
                                value={formData.name}
                                onChange={handleChange}
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="phone">:</label>
                            <input
                                type="tel"
                                id="phone"
                                name="phone"
                                className="form-control"
                                required
                                value={formData.phone}
                                onChange={handleChange}
                            />
                        </div>

                        <div className="form-group">
                            <label htmlFor="address"> :</label>
                            <input
                                type="text"
                                id="address"
                                name="address"
                                className="form-control"
                                required
                                value={formData.address}
                                onChange={handleChange}
                            />
                        </div>

						<button type="submit" className="btn"> </button>
					</form>

					<h2 className={`form-success ${(successVisible) ? 'show' : 'hidden'}`}>  !</h2>

					<h2 className={`form-empty ${(emptyCartVisible) ? 'show' : 'hidden'}`}> !</h2>
					
				</div>
			</div>
		</div>
	);
}