//   URL  API
const urlPart = 'https://6558482de93ca47020a93e41.mockapi.io/';

// ',    URL
const urls = {
    apiCatalog : urlPart + 'catalog',
    apiCategory: urlPart + 'category',
};

// ',         URL
const apiUrl = {
    urlPart,
    catalog           : urlPart + 'catalog', // URL  
    catalogByCategory : urls.apiCatalog + '?catid=', // URL     
    category          : urlPart + 'category', // URL  
    hotOffer          : urls.apiCatalog + '/?hotoffer=yes', // URL   
    search            : urls.apiCatalog + '/?search=', // URL    
};

export default apiUrl;
